html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.no-wrap {
  white-space: nowrap;
}

.hidden {
  display: none;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
  object-fit: contain;
  object-position: center;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

span, b {
  color: inherit;
  font: inherit;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

[class*=icon-]::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

* {
  font-family: "TT Livret Display Trl";
  font-style: normal;
}

[class*=__container], .container {
  max-width: 1770px;
  padding: 0 15px;
  margin: 0 auto;
}

body {
  background-color: #FAFAFA;
}

