}
@media (max-width: 740px) {
  .about-grid__item-text {
    margin-top: 10px;
  }
}
.about-grid__item-image {
  display: block;
  width: 350px;
  height: 350px;
  object-fit: cover;
  border-radius: 10px;
}
@media (max-width: 1770px) {
  .about-grid__item-image {
    width: 200px;
    height: 200px;
  }
}
@media (max-width: 740px) {
  .about-grid__item-image {
    width: 100px;
