.certifs__tags p {
  padding: 19px 60px;
  background: rgba(207, 218, 226, 0.4);
  border-radius: 100px;
  font-family: "Inter";
  font-weight: 500;
  font-size: 18px;
  line-height: 122%;
  text-align: center;
  color: #87ABBA;
}

.checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  max-width: max-content;
  user-select: none;
}
.checkbox > div {
  position: relative;
  width: 12px;
  height: 12px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.checkbox > div input {
  position: absolute;
  inset: 0;
  visibility: hidden;
  cursor: pointer;
}
.checkbox > div i {
  cursor: pointer;
  font-size: 6px;
  line-height: 1;
  transform: scale(0);
  transition: 0.3s ease;
}
.checkbox > div:has(input:checked) i {
  transform: scale(1);
  color: rgba(255, 255, 255, 0.5);
}
.checkbox > span {
  font-family: "Inter";
  cursor: pointer;
  font-weight: 300;
  font-size: 18px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.35);
}

.contacts-popup {
  position: absolute;
  z-index: 999;
  width: 300px;
  background-color: white;
  right: 0;
  top: calc(100% + 10px);
  padding: 20px 20px 36px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
}
.contacts-popup::before {
  content: "";
  position: absolute;
  top: -12px;
  left: 0;
  right: 0;
  bottom: 0;
}
.contacts-popup__title {
  position: relative;
  font-family: "Inter";
  font-weight: 500;
  color: #256885;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  text-transform: uppercase;
}
.contacts-popup__list {
  position: relative;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.contacts-popup__list li {
  display: flex;
  align-items: center;
  gap: 10px;
}
.contacts-popup__list li i {
  font-size: 32px;
  color: rgba(37, 104, 133, 0.5);
}
.contacts-popup__list li span {
  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  color: #5D6366;
}
.contacts-popup__btn {
  position: relative;
  margin-top: 40px;
  display: block;
  font-family: "Inter";
  color: white;
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  padding: 14px 10px;
  background: rgba(37, 104, 133, 0.5);
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .contacts-popup__btn:hover {
    background: rgb(37, 104, 133);
  }
}

