.about {
  min-height: 1175px;
  background: url(../img/bg/about.webp) no-repeat center, #D9E2E8;
  background-size: cover;

  @media (min-width: 2641px) {
    background-size: contain;
  }

  @media (max-width: 940px) {
    min-height: 800px;
  }

  @media (max-width: 740px) {
    min-height: 400px;
    height: 100vh;
    max-height: 1200px;
    background-position: center bottom;
    background-size: 180%;
  }

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__text {
    font-family: 'Inter';
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: #5D6366;
    padding: 40px 0;
    max-width: 690px;
    margin-left: 50%;

    @media (max-width: 940px) {
      margin-left: 50px;
    }

    @media (max-width: 740px) {
      padding: 20px 0;
      margin-left: 0;
      text-align: right;
      font-size: 12px;
    }
  }

  &__link {
    font-family: 'Inter';
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #256885;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;

    i {
      font-size: 8px;
      vertical-align: middle;
      margin-inline-start: 10px;

      &::before {
        width: auto;
      }
    }

    @media (any-hover: hover) {
      &:hover {
        font-weight: 500;
      }
    }
  }
}
